/* sortable select */
.treedropdownfield.sortable .Select-value .next,
.treedropdownfield.sortable .Select-value .prev,
.treedropdownfield.sortable .Select-value:after {
    display: none;
    padding: 0.5em;
    border-left: 1px solid #c9e6f2;
    margin: -0.5em 0;
}

.treedropdownfield.sortable .Select-value:after {
    display: inline-block;
    content: '::';
}

.treedropdownfield.sortable .Select-value .prev {
    border-left: 0;
    border-right: 1px solid #c9e6f2;
}
/* .active */
.treedropdownfield.sortable .Select-value.active {
    background: #1f3944;
    color: #fff;
}

.treedropdownfield.sortable .Select-value.active .Select-value-icon,
.treedropdownfield.sortable .Select-value.active:after {
    display: none;
}
.treedropdownfield.sortable .Select-value.active .next,
.treedropdownfield.sortable .Select-value.active .prev {
    display: inline-block;
}
